.Root {
  background-color: #22303c;
  min-height: 100vh; /* Full height of the viewport */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Spread the content (Content centered, Logos at the bottom) */
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1; /* This makes the content (Name & Body) centered */
}

.Name,
.Body {
  color: white;
  font-family: monospace;
}

.Name {
  position: relative;
  font-size: 35px;
  margin-top: 200px;
  margin-bottom: 10px;
}

.Body {
  position: relative;
  font-size: 20px;
  flex-grow: 1;
  color: white;
}

.Lastfm {
  position: relative;
}

.Username {
  position: absolute;
  left: 0;
  top: 0;
  margin: 20px;
  font-family: monospace;
}

.line {
  display: inline-block; /* Allows each line to be treated separately */
  animation:
    typing 3s steps(40, end) forwards,
    blink-caret 0.75s step-end infinite;
}

.typewriter p::after {
  content: "|";
  opacity: 1;
  animation: blink 1s step-end infinite;
}

.TextWithCursor {
  white-space: pre-wrap; /* Preserve line breaks */
  font-family: monospace; /* Optional: Monospace font for the typing effect */
}

.cursor {
  display: inline-block;
  width: 1px;
  background-color: transparent;
  animation: blink 1s step-end infinite;
}

#Logos img {
  justify-content: space-between;
  margin: 10px;
  width: 50px;
  -webkit-filter: invert(100%); /* Safari/Chrome */
  filter: invert(100%);
  margin-top: auto;
}

footer {
  font-family: monospace;
  font-size: 15px;
}

footer a:visited,
a:link {
  color: white;
}

@keyframes blink {
  from,
  to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
